*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body{
    background-color: white;
}

.card-front{
    position: relative;
    border-radius: 12px;
    box-shadow: 20px 20px 40px rgba(0, 0, 0, 0.185);
}

.card-front-container{
    position: absolute;
    left: 20%;
    top: 20%;
    
}

.card-back{
    position: relative;
    border-radius: 12px;
    box-shadow: 20px 20px 40px rgba(0, 0, 0, 0.185);
}

.card-back-container{
    position: absolute;
    left: 23.5%;
    top: 50%;

}

.card-back-data{
    position: absolute;
    z-index: 1;
    font-family: 'Space Grotesk', sans-serif;
    color: white;
    top: 42.25%;
    left: 77.5%;
    font-weight: 400;
    letter-spacing: .1rem;
}

.card-value{
    position: absolute;
    z-index: 1;
    bottom: 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    padding: 2rem;
    width: 440px;
    font-family: 'Space Grotesk', sans-serif;
    color: white;
  
}

.card-value h1{
    width: 100%;
    grid-column-start: 1;
    grid-column-end: 4;
    font-weight: 500;
    letter-spacing: .25rem;
}

.card-value h2{
    grid-column-start: 1;
    grid-column-end: 3;
    font-size: 1rem;
    font-weight: 400;
    letter-spacing: .1rem;
    align-self: end;
}

.card-value h2:last-of-type{
    grid-column-start: 3;
    grid-column-end: 4;
    align-items: end;
    justify-self: end;
}

.card-front-logo{
    position: absolute;
    z-index: 1;
    top: 0;
    padding: 2rem;
}

.cards{
    background-image: url("../src/images/bg-main-desktop.png");
    background-repeat: no-repeat;
    background-size: cover;
    width: 35vw;
    height: 100vh;
}

main{
    display: flex;
}

.form-container{
    width: 65vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

form{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 400px;
    
}

.date-container{
    display: flex;
    flex-direction: column;
}

.data-input-container{
    display: flex;
    flex-direction: row;
    gap: 1rem;
}

.data-input-container input{
    width: 100px;
}

.bottom-container{
    display: flex;
    flex-direction: row;
    gap: 1rem;
}

form label{
    margin-bottom: -1rem;
}

.bottom-container label{
    margin: 0;
}

.cvc-container{
    display: flex;
    flex-direction: column;
}

.cvc-container input{
    width: 167.5px;
}
fieldset{
    border: none;
  
}

label{
    font-family: 'Space Grotesk', sans-serif;
    font-weight: 500;
    color: hsla(0,0,0,1);
}

input{
    border: .1rem solid #8e8593;
    padding: 1rem 2rem;
    border-radius: 10px;
    font-family: 'Space Grotesk', sans-serif;
    font-weight: 500;
}

button{
    background-color: #000000;
    padding: 1rem 2rem;
    border: none;
    border-radius: 10px;
    color: white;
    font-family: 'Space Grotesk', sans-serif;
    font-weight: 600;
    transition: background-color .15s;
}

button:hover{
    cursor: pointer;
    background-color:hsla(266, 100%, 64%, 1);
}